import React, { useState, useEffect } from "react";
import "./styless.css";

function App() {
  const student = [
    {
      id: 0,
      name: "김유훈",
      participation: true,
    },
    {
      id: 1,
      name: "김일주",
      participation: true,
    },
    {
      id: 2,
      name: "김라빈",
      participation: true,
    },
    {
      id: 3,
      name: "박고운",
      participation: true,
    },
    {
      id: 4,
      name: "박예솔",
      participation: true,
    },
    {
      id: 5,
      name: "박지성",
      participation: true,
    },
    {
      id: 6,
      name: "송혜림",
      participation: true,
    },

    {
      id: 7,
      name: "이민지",
      participation: true,
    },

    {
      id: 8,
      name: "이효정",
      participation: true,
    },
    {
      id: 9,
      name: "박민석",
      participation: true,
    },
    {
      id: 10,
      name: "정승희",
      participation: true,
    },
    {
      id: 11,
      name: "유정인",
      participation: true,
    },
    {
      id: 12,
      name: "성진하",
      participation: true,
    },
    {
      id: 13,
      name: "유성재",
      participation: true,
    },
    {
      id: 14,
      name: "안대희",
      participation: true,
    },
    {
      id: 15,
      name: "손지우",
      participation: true,
    },
  ];
  const handleClick = (id) => {
    const data = name.map((list) =>
      list.id === id ? { ...list, participation: !list.participation } : list
    );
    setName(data);
  };
  let names = [{}];
  let psd = "";
  const [name, setName] = useState(student);
  const [group, setGroup] = useState(names);
  const [results, setResults] = useState("");
  const [add, setAdd] = useState("");

  const party = name.map((list) => {
    return list.participation ? (
      <li key={list.id}>
        {list.name}
        <button onClick={() => handleClick(list.id)}>빼기</button>
      </li>
    ) : (
      ""
    );
  });
  const unparty = name.map((list) => {
    return list.participation ? (
      ""
    ) : (
      <li key={list.id}>
        {list.name}
        <button onClick={() => handleClick(list.id)}>추가</button>
      </li>
    );
  });

  let arrary = [];
  let count = 0;

  const session = () =>
    name.map((list) => {
      list.participation === true ? arrary.push(list.name) : count++;
    });
  const handleCheck = async (time) => {
    setResults(time);
    arrary = [];
    count = 0;
    names = [];
    session();
    for (let i = 0; arrary.length !== 0; i++) {
      names.push(
        arrary.splice(
          arrary.indexOf(arrary[Math.floor(Math.random() * arrary.length)]),
          1
        )[0]
      );
    }
    setGroup(names);
  };

  let wqe = 0;
  const result =
    group.length > 1 ? (
      group.map((list, i, a, b = 1) => {
        if (i % 2 === 0) {
          wqe++;
          return (
            <div className="result">
              <h2>{wqe}조</h2>
              {list}
            </div>
          );
        } else {
          return <div className="result1">{list}</div>;
        }
      })
    ) : (
      <div>결과</div>
    );
  let qwe = 0;
  const three =
    group.length > 1 ? (
      group.map((list, i, a, b = 1) => {
        if (i % 3 === 0) {
          qwe = qwe + 1;
          return (
            <div className="result">
              <h2>{qwe}조</h2>
              {list}
            </div>
          );
        } else if (i % 3 === 1) {
          return <div className="result1">{list}</div>;
        } else {
          return <div className="result2">{list}</div>;
        }
      })
    ) : (
      <div>결과</div>
    );
  let number = 16;
  const handleDataAdd = () => {
    const asd = name.concat({
      ...name,
      id: name.length + number,
      name: add,
      participation: true,
    });
    setName(asd);
    console.log(name);
    setAdd("");
  };

  return (
    <div>
      <h1> A반 그룹 짜기 </h1>
      <span>추가할 이름: </span>
      <input
        value={add}
        name={add}
        onChange={(e) => setAdd(e.target.value)}
      ></input>
      <button onClick={() => handleDataAdd()}>추가하기</button>
      <div className="box">
        <div className="participation">
          <h2>참가 인원</h2>
          {party}
        </div>
        <div className="unparticipation">
          <h2>불참가 인원</h2>
          {unparty}
        </div>
      </div>
      <div className="buttonBox">
        <button onClick={() => handleCheck(true)}>2인 그룹</button>
        <button onClick={() => handleCheck(false)}>3인 그룹</button>
      </div>

      {results ? result : <div>{three}</div>}
    </div>
  );
}

export default App;
